.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
}

html {
  margin: 0;
}

body {
  background-color: #c3ebf9;
  margin: 0;
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, p, nav {
  border: none;
  margin: 0;
  padding: 0;
}

ul {
  border: none;
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  border: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.header {
  height: 300px;
  width: 100%;
  background-image: url("banner-909710_640.fbc0859d.jpg");
  background-position: center;
  background-size: cover;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .header {
    background-image: url("banner-909710_1280.03887b35.jpg");
  }
}

@media (min-width: 1200px) {
  .header {
    background-image: url("banner-909710_1920.589d3179.jpg");
  }
}

.header-photo {
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 4%;
  right: 10%;
}

@media screen and (min-width: 768px) {
  .header-photo {
    right: 35%;
  }
}

@media screen and (min-width: 1200px) {
  .header-photo {
    right: 42%;
  }
}

.header-title {
  text-align: center;
  margin-top: 16px;
  display: block;
}

.header-info {
  text-align: center;
  margin-top: 8px;
  display: block;
}

.hero {
  grid-template: "hero-aside hero-info" 1fr
  / 400px 1fr;
  margin-top: 32px;
  display: grid;
}

.hero-aside {
  background-color: bisque;
  border-right: 1px solid #000;
  grid-area: hero-aside;
  padding: 16px;
}

.hero-info {
  grid-area: hero-info;
  padding: 16px;
}

.footer {
  height: 100px;
  background-color: #082b76;
}

